import React, { Component } from 'react';
import axios from 'axios'; 
import { fetchShopifyProduct } from '../../../../actions/cartActions';
import '../scss/PopUpProducts.scss'
import ProductImage from './ProductImage'; 
import parse from "html-react-parser";
import { themePostTracking } from '../../../../actions/moredataAction';

async function getProductDetails(sku) {
       const baseUrl = `${window.location.origin}/rest/V1`;

       try {
              // 1. Get basic product details
              const productResponse = await axios.get(`${baseUrl}/products/${sku}`);
              const productData = productResponse.data;

              // 2. If the product is configurable, get child (variant) details
              if (productData.type_id === 'configurable') {
                     const variantsResponse = await axios.get(`${baseUrl}/configurable-products/${sku}/children`);
                     const variantData = variantsResponse.data;
              }

       } catch (error) {
              console.error('Error retrieving product details:', error.response?.data || error.message);
       }
}


async function loadCustomerSection(sections = ['cart']) {
       const sectionQuery = sections.join(',');
       try {
             await axios.get(`${window.location.origin}/customer/section/load/`, {
                     params: {
                            sections: sectionQuery,
                            force_new_section_timestamp: true,
                            _: Date.now(), // Current timestamp to prevent caching
                     },
                     withCredentials: true,
              });
              window.location.href = `${window.location.origin}/checkout/cart/`

       } catch (error) {
              console.error('Error loading sections:', error.response?.data || error.message);
       }
}


async function addToCart(productSku, qty = 1, cartId = null, token = null) {
       const baseUrl = `${window.location.origin}/rest`;
       const endpoint = token
              ? `${baseUrl}/V1/carts/mine/items`
              : `${baseUrl}/V1/guest-carts/${cartId}/items`;

       const headers = token ? { Authorization: `Bearer ${token}` } : {};

       const data = {
              cartItem: {
                     sku: productSku,
                     qty: qty,
                     quote_id: cartId,
              },
        }

       try {
              await axios.post(endpoint, data, { headers });
              loadCustomerSection(['cart', 'directory-data', 'messages']);

       } catch (error) {
              console.error('Error adding to cart:', error.response?.data || error.message);
       }
  }


function getCookie(key) {
       const cookies = document.cookie.split("; ");
       for (let cookie of cookies) {
              const [cookieKey, cookieValue] = cookie.split("=");
              if (cookieKey === key) return decodeURIComponent(cookieValue);
       }
       return null; // Return null if the key is not found
}
const codeCheck = async (product_sku) => {
       const guestCartId = await axios.post(`${window.location.origin}/rest/V1/guest-carts`).then(res => res.data);
       addToCart(product_sku, 1, guestCartId);
       getProductDetails(product_sku)

}
const BtnClick = (url, productId, product_sku) => event => {
       const convertURL = btoa(url)
       const myCookieValue = getCookie("form_key");

       const cartURL = `${window.location.origin}/checkout/cart/add/uenc/${convertURL}/product/${productId}/`


       const formData = new FormData();
       formData.append('product', productId);
       formData.append('selected_configurable_option', '');
       formData.append('related_product', '');
       formData.append('item', productId);
       formData.append('form_key', myCookieValue);
       formData.append('qty', 1);


       codeCheck(url, productId, product_sku)
       axios.post(cartURL, formData, {
              headers: {
                     'Content-Type': 'multipart/form-data'
              }
       })
              .then(response => {
                     console.log('Response:', response.data);
              })
              .catch(error => {
                     console.error('Error:', error);
              });


}

class PopUpProducts extends Component {
       state = { prdImgLoaded: false, noImg: false, itemDetails: {}, isLoading: true }
         componentDidMount() {
              const { item, wall } = this.props;
              let { itemDetails } = this.state;
              
              if (item && item.isProduct) {
                     // Set isLoading to true before starting the async operations
                     this.setState({ isLoading: true });

                     const fetchPromises = item.products.map(i => {
                            const fetchUrl = i.url;

                           // if (wall?.ProductSetting?.Button?.newTab == 1) {
                            if (wall?.ProductSetting?.Button?.pdp_target == 1) {
                                   itemDetails[i.id] = { data: null, showCart: false };
                                   this.setState({ itemDetails });
                                   return Promise.resolve(); // Skip fetch, but still keep promise to handle completion
                            } else {
                                   if (fetchUrl && !String(fetchUrl).includes(".html") && i.createdFrom != "") {
                                          const isMagento = i.createdFrom === "magento" || false;

                                          return fetchShopifyProduct(fetchUrl, isMagento, {
                                                 productId: i.id,
                                                 storeId: i.storeId,
                                          })
                                                 .then(res => {
                                                        if (isMagento && res.status === false) {
                                                               itemDetails[i.id] = {
                                                                      data: { available: true },
                                                                      showCart: wall?.ThemeInfo.ownerId !== 140717,
                                                               };
                                                        } else {
                                                               const productData = (isMagento && res?.data?.data) || res?.data;
                                                               const formatProduct = isMagento ? { available: true } : {};
                                                               itemDetails[i.id] = {
                                                                      data: productData?.id ? { ...productData, ...formatProduct } : null,
                                                                      showCart: productData?.id && wall?.ThemeInfo.ownerId !== 140717,
                                                               };
                                                        }
                                                        this.setState({ itemDetails });
                                                 })
                                                 .catch(error => {
                                                        itemDetails[i.id] = { data: null, showCart: false };
                                                        this.setState({ itemDetails });
                                                 });
                                   } else {
                                          itemDetails[i.id] = { data: null, showCart: false };
                                          this.setState({ itemDetails });
                                          return Promise.resolve(); // Skip fetch, but still keep promise to handle completion
                                   }
                            }
                     });

                     // Wait for all fetch operations to complete, then set isLoading to false
                     Promise.all(fetchPromises).then(() => {
                            this.setState({ isLoading: false });
                     });
              }

       }

       openUrl = (url) => {
              const { ProductSetting } = this.props;
              const Button = ProductSetting;
              var newUrl = url;
              if (parseInt(Button.newTab) == 2) window.parent.location.href = newUrl
              else window.open(newUrl, '_blank');
       }
       tracking = (wall, product) => {
              const { item } = this.props
              themePostTracking({ type: 1, action: 2, wall: wall?.ThemeInfo?.webId, product_id: product.id, post: item.referenceId, owner: wall?.ThemeInfo?.ownerId, feed: item.feedId, currency: product.currency, price: Number(product.price) > 0 ? product.discount : product.price })
       }

       render() {
              const { item, products, wall, openCart, ProductSetting } = this.props;
              const { itemDetails, isLoading } = this.state
              const { CatalogueTitle, Price, ProductTitle, Button} = ProductSetting

              return  Object.keys(products || {}).length ? <div className="tb_pro__container">
                     <div className={`tb_pro__heading tb-cTBfont-${CatalogueTitle.variant}`}>{parse(CatalogueTitle.text)}</div>
                     <div className="tb_pro__wrap">
                            {products.map((product, index) => <div className="tb_pro__item" key={index}>
                                   <div className={`tb_pro__wrap_in`}>
                                          <div className='tb_pro__img_wrapper'>
                                                 <ProductImage key={`pro-${product.id}`} ImageClass="tb_pro__image" media={product.image} size={false} itemDetails={itemDetails[product.id]|| null} />
                                          </div>
                                          {(product?.title) ?
                                                 <div className={`tb_pro__title tb-cTBfont-${ProductTitle.variant}`}>{parse(product.title)}</div> : null}

                                          {Number(product.price) > 0 || Number(product.discount) > 0 ?
                                                 <div className="tb_pro__price_wrap">
                                                        <div className={`tb_pro__price tb-cTBfont-${Price.variant}`}>
                                                               {product.currency}{Number(product.discount) > 0 ? product.discount : product.price}
                                                        </div>
                                                        {Number(product.discount) > 0 && product.discount != product.price ? <div className={`tb_pro__discount_price tb-cTBfont-${Price.variant}`}>{product.currency}{product.price}</div> : null}
                                                 </div>
                                                 : null}

                                                 {itemDetails && Object.keys(itemDetails).length && itemDetails[product.id] && itemDetails[product.id]?.showCart ?
                                                 <div className={`tb_pro__link`}>
                                                    <div onClick={itemDetails[product.id]?.data?.available ? () => openCart(product.id, product, itemDetails[product.id], item) : null} className={`tb_pro__btn ${itemDetails[product.id]?.data?.available ? `` : `tb_pro_sold`} tb-cTBfont-${Button.variant} ${isLoading ? `tb_pro_sold` : ``}`} >{itemDetails[product.id]?.data?.available ? Button.text : 'Sold Out'}</div>
                                                 </div>
                                                 :
                                                 <div className={`tb_pro__link`}>
                                                    <div onClick={() => { this.openUrl(product.url); this.tracking(wall, product) }} className={`tb_pro__btn tb-cTBfont-${Button.variant} ${isLoading ? ` tb_pro_sold` : ``}`} target={wall && wall.User && wall.User.id != `102810` ? "_blank" : ''}>{Button.text}</div>
                                                 </div>}
                                   </div>
                            </div>
                            )}</div>
              </div> : null 
       }
}


export default PopUpProducts
