import React, { useEffect } from "react";
import { connect } from 'react-redux'; 
import { sharePostPopUP } from '../../../../actions/OtherActions';
import {  themePostTracking, gtmAnalytics } from '../../../../actions/moredataAction';

import { isTagShop, isTagbox } from '../../../../actions/api'; 
import { ensureFullyQualifiedUrl } from "../../../../utils/Common";

const PopUpShare = ({ sharePostPopUP, item, wall, color }) => {
    const { webId, wallId, ownerId } = wall.ThemeInfo;

    // Bind Twitter event once on mount
    useEffect(() => {
        if (window.twttr) {
            window.twttr.ready((twttr) => {
                window.twttr.events.bind('tweet', onTwitterClick);
            });
        }
    }, []);

    // Share on Facebook
    const onShareFacebooklink = (shareURL) => (event) => {
        window.open(shareURL, "_target");
            themePostTracking({
                type: 2,
                action: 2,
                wall: isTagShop ? webId : wallId,
                feed: item.feedId,
                post: item.referenceId ? item.referenceId : item.id,
                owner: ownerId,
            });
        gtmAnalytics("Tagbox", "popup_share_click", "popup_share", isTagShop ? webId : wallId);
    };

    // Share on Twitter
    const onTwitterClick = (event) => {
        if (event) {
                themePostTracking({
                    type: 2,
                    action: 2,
                    wall: isTagShop ? webId : wallId,
                    feed: item.feedId,
                    post: item.referenceId ? item.referenceId : item.id,
                    owner: ownerId,
                });
            gtmAnalytics("Tagbox", "popup_share_click", "popup_share", isTagShop ? webId : wallId);
        }
        window.open(item.share.twitter, "_target");
    };

    // Share on LinkedIn
    const linkedinShareCount = (shareURL) => (event) => {
            themePostTracking({
                type: 2,
                action: 2,
                wall: isTagShop ? webId : wallId,
                feed: item.feedId,
                post: item.referenceId ? item.referenceId : item.id,
                owner: ownerId,
            });
        gtmAnalytics("Tagbox", "popup_share_click", "popup_share", isTagShop ? webId : wallId);
        window.open(shareURL, "_target");
    };

    // Open link in new tab
    const openLink = (link) => (event) => {
        window.open(ensureFullyQualifiedUrl(link), "_blank");
    };

    const fontStyle = {
        color: color,
    };

    return (
        <div className={`tb_post_modal_post_footer_social_`}>
            <div className="tb_post_modal_share_ico">
                <div className="tb_post_modal_social_ico_list___">
                    <div onClick={onShareFacebooklink(item.share.facebook)}>
                        <div className="tb_post_modal_share_button tb__icon tb-facebook" style={fontStyle}><div></div></div>
                    </div>
                </div>
                <div className="tb_post_modal_social_ico_list___">
                    <div className="tb_post_modal_share_button tb__icon tb-twitter" onClick={onTwitterClick} style={fontStyle}><div></div></div>
                </div>
                <div className="tb_post_modal_social_ico_list___">
                    <div className="tb_post_modal_share_button tb__icon tb-linkedin" onClick={linkedinShareCount(item.share.linkedin)} style={fontStyle}><div></div></div>
                </div>
                {isTagbox && item.link && (
                    <div className="tb_post_modal_social_ico_list___" onClick={() => sharePostPopUP(item)}>
                        <div className="tb_post_modal_share_button tb__icon tb-mail" style={fontStyle}></div>
                    </div>
                )}
                {item.link && (
                    <div className="tb_post_modal_social_ico_list___ tb_post_link__" onClick={openLink(item.link)}>
                        <div className="tb_post_modal_share_button tb__icon tb-link" style={fontStyle}></div>
                    </div>
                )}
            </div>
        </div>
    );
};

const mapDispatchToProps = {
    sharePostPopUP,
};

export default connect(null, mapDispatchToProps)(PopUpShare);