import React, { PureComponent, Suspense } from "react";
import parse from "html-react-parser";
import Rating from "./PopUpRating"; 
import { isTagShop, isEmbed } from "../../../../actions/api";
import { STRING_TO_URL_CONVERT } from "../../../../utils/Common";
const PostContentHighlight = React.lazy(() => import("../../../Themes/Elements/PostContent/PostContentHighlight"));

export default class PopUpContent extends PureComponent {
  constructor(props) {
    super(props);
    this.upperDivRef = React.createRef();
    this.innerDivRef = React.createRef();
  }
  state = {
    scrollAmount: 1,
    scrollDirection: 1,  
    contentHeightInner: 100,
    contentHeightUpper: 100
  };

  componentDidMount() {
    const contentHeightUpEle = this.upperDivRef.current;
    const contentHeightEle = this.innerDivRef.current;
    if (contentHeightEle) {
      this.setState({
        contentHeightInner: contentHeightEle.clientHeight,
        contentHeightUpEle: contentHeightUpEle.clientHeight
      })
    }
  }
  render() {
    const { contentTitle, item, CardStyle, allSize, isProduct, socialAction } = this.props;
    const { contentHeightInner, contentHeightUpEle } = this.state
    const constentHeight = !isProduct && !isTagShop && allSize.winWidth > 991 ? allSize.height - (socialAction ? 100 : 0) : null
    let contentData =  STRING_TO_URL_CONVERT(this.props.contentData.text);
    const contentDecorateClass = String(this.props.contentData).length > 0 && CardStyle.style.textDecoration ? ' tb_post_modal_text_decorate' : ''
    if (CardStyle.widgetTheme === 47) document.querySelector('html').style.overflow = "auto";
    
    const contentScroll = (!isEmbed && (contentHeightInner > (constentHeight ? constentHeight : contentHeightUpEle))) ? true : false
    return <div className={`tb_post_modal_post_wrapper  ${contentScroll ? `tb_scroll_auto` : ``}`} key={`content_${item.id}`} style={{ height: item.type != 1 ? constentHeight : '100%' }}>
      <div className="tb_post_modal_post_wrap_in" ref={this.upperDivRef}>
        <div className={`tb_post_modal_post_content`}>
          {item.rating != null && item.rating ? <Rating rating={item.rating} network={item.network} /> : null}
          <div className={`tb_post_modal_content tb-cTBfont-${CardStyle.style.variant} ${contentDecorateClass}`} ref={this.innerDivRef}>
            {contentTitle ? <div className="tb_post_bold_txt">{parse(contentTitle)}</div> : null}
            <Suspense fallback={<></>}> <PostContentHighlight content={contentData} CardStyle={CardStyle} /></Suspense>
          </div>
        </div>
      </div>
    </div>
  }
}
