import React, { PureComponent } from "react";

const ThemeRender = ({network}) => {
    const networkStyles = {
        3:  { className: 'tb_rating_ico__ tb__icon tb-default-star', color: '#1b74e4',fontSize:30 },
        19: { className: 'tb_rating_ico__ tb__icon tb-yelp-star', color: '#e00707',fontSize:30 },
        23: { className: 'tb_rating_ico__ tb__icon tb-default-star', color: '#ff385c',fontSize:30 },
        28: { className: 'tb_rating_ico__ tb__icon tb-default-star', color: '#F8B90C',fontSize:30 },
        29: { className: 'tb_rating_ico__ tb__icon tb-default-star', color: '#613983',fontSize:30 },
        33: { className: 'tb_rating_ico__ tb__icon tb-trustpilot', color: '#000',fontSize:30 },
        35: { className: 'tb_rating_ico__ tb__icon tb-star-fill', color: '#34E0A1',fontSize:30 },
    };
    const { className, color, fontSize } = networkStyles[network.id] || {
        className: 'tb_rating_ico__ tb__icon tb-default-star',
        color: '#F8B90C',
        fontSize:30
    };

    return <div className={className} style={{ color, fontSize }} tb-network={network.id}><div></div></div>;
}
export default class Rating extends PureComponent {
    render() {
        const { rating, network } = this.props;
        return (
            <div className="tb__modal_rating__">
            {
                rating?[...Array(rating)].map((el, index) =><ThemeRender key={index} network={network}/>):null
            }
            </div>
        );
    }
}
