import React, { PureComponent, Suspense } from "react";
import { GET_IMAGE_SIZE } from "../../../../utils/Common";
import PostBranding from "../../../Themes/Elements/Branding/PostBranding";
const PopUpMediaSlider = React.lazy(() => import('./PopUpMediaSlider'));
const PopUpImage = React.lazy(() => import('./PopUpImage'));
const PopUpVideo = React.lazy(() => import('./PopUpVideo'));
const PopUpAudio = React.lazy(() => import('./PopUpAudio'));

export default class PopUpMedia extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      winSize: {
        width: window.innerWidth,
        height: window.innerHeight,
      },
      mediaSize: {
        height: 0,
        width: 0,
        paddingBottom: 0,
      },
    };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.Post !== prevState.Post) {
      return {
        ...prevState,
        Post: nextProps.Post,
      };
    }
    return null;
  }

  componentDidMount() {
    const { Post } = this.props;
    window.addEventListener('resize', this.handleResize);
    this._isMounted = true;
    this.onMediaLoad(Post);
  }

  componentDidUpdate(prevProps) {
    if (this.props.Post !== prevProps.Post) this.onMediaLoad(this.props.Post);
  }

  componentWillUnmount() {
    this._isMounted = false;
    window.removeEventListener('resize', this.handleResize);
  }

  handleResize = () => {
    this.setState({
      winSize: {
        width: window.innerWidth,
        height: window.innerHeight,
      },
    }, () => {
      const { mediaSize } = this.state;
      this.setSize(mediaSize.height, mediaSize.width);
    });
  }

  setSize = (height, width) => {
    const { hideContent } = this.props;
    const { winSize } = this.state;
    const scale = Math.min((winSize.width - 535) / width, (winSize.height - (90 + (hideContent ? 69 : 0))) / height);

    const mediaSize = {
      height: height * scale,
      width: width * scale,
      paddingBottom: (height * 100) / width,
    };

    const allSize = {
      winHeight: winSize.height,
      winWidth: winSize.width,
      height: mediaSize.height,
      width: mediaSize.width,
      paddingBottom: mediaSize.paddingBottom,
    };

    this.props.allSizeCallback(allSize);

    this.setState({
      mediaSize: {
        height: mediaSize.height,
        width: mediaSize.width,
        paddingBottom: mediaSize.paddingBottom,
      },
    });
  }

  onMediaLoad = async (Post) => {
    const { height, width } = Post.media;
    if (height && width) {
      this.setSize(height, width);
    } else {
      const imageContainer = document.querySelector(`.tb_media-${Post.id}`);
      const getHeight = imageContainer ? parseInt(imageContainer.getAttribute("data-height"), 10) : 0;
      const getWidth = imageContainer ? parseInt(imageContainer.getAttribute("data-width"), 10) : 0;

      if (getWidth > 0 && getHeight > 0) {
        this.setSize(getHeight, getWidth);
      } else {
        try {
          const { width, height } = await GET_IMAGE_SIZE(Post.media.image.small);
          if (this._isMounted) {
            this.setSize(height, width);
          }
        } catch (error) {
          if (this._isMounted) {
            this.setSize(1000, 1000);
          }
        }
      }
    }
  }

  render() {
    const { Post, ThemeInfo, onUpdateProdImgIdx } = this.props;
    const { winSize, mediaSize } = this.state;

    const mediaStyle = {
      maxHeight: winSize.width > 991 ? mediaSize.height : null,
      maxWidth: winSize.width > 991 ? mediaSize.width : null,
      aspectRatio: `${mediaSize.width} / ${mediaSize.height}`,
      flexBasis: winSize.width > 991 ? mediaSize.height : null,
    };

    const mediaStyleIn = { paddingBottom: `${mediaSize.paddingBottom}%`, width: winSize.width > 991 ? mediaSize.width : null };

    return (
      <div className="tb_post_modal_media_holder" style={mediaStyle}>
        {ThemeInfo?.branding ? <PostBranding size={2} themeId={ThemeInfo.id} /> : null}
        <div className="tb_post_modal_media" style={mediaStyleIn}>
          <div className="tb_post_modal_media_">
            {Post.mediaList && Post.mediaList.length > 0 ? (
              <Suspense fallback={<></>}>
                <PopUpMediaSlider winSize={winSize} Post={Post} mediaSize={mediaSize} ThemeInfo={ThemeInfo} onUpdateProdImgIdx={onUpdateProdImgIdx} />
              </Suspense>
            ) : [2,4].includes(Post.type) ? (
              <Suspense fallback={<></>}>
                <PopUpImage winSize={winSize} Post={Post} Item={false} Products={Post.products}  mediaSize={mediaSize} ThemeInfo={ThemeInfo} opacity={1} hotspot />
              </Suspense>
            ) : [3,5].includes(Post.type) ? (
              Post.isAudio ? (
                <Suspense fallback={<></>}>
                  <PopUpAudio Post={Post} />
                </Suspense>
              ) : (
                <Suspense fallback={<></>}>
                  <PopUpVideo Post={Post} key={Post.id} mediaSize={mediaSize}/>
                </Suspense>
              )
            ) : null}
          </div>
        </div>
      </div>
    );
  }
}