import React, { PureComponent } from "react";
import Network from '../../../Themes/Elements/Network/Network'
import { isInstaUser } from '../../../../utils'
import Author from "../../../Themes/Elements/Author/Author";
import { isEmbed, isSocialEmbed, isSocialWall, isTagShop } from '../../../../actions/api'
import { ensureFullyQualifiedUrl, timeAgo } from "../../../../utils/Common";
import Time from "../../../Themes/Elements/Time/Time";

export default class PopUpAuthor extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      picture: props.Post.author.picture
    }
  }
  UNSAFE_componentWillReceiveProps(nextProps) {
    const { Post } = nextProps;
    this.setState({ picture: Post.author.picture })
  }
  render() {
    const { Post, CardStyle } = this.props;
    const { name, username } = Post.author
    const authorStatus = CardStyle.author.status
    
    const authorUserName = `${username && username.length > 0 ? `@` : ``}${username}`
    const showAuthor = !((isTagShop && !authorStatus) || authorStatus == 0 || isInstaUser(name)) ? true : (isSocialWall || isSocialEmbed) ? true : false
    const isDetail = Post.timePost == 0 || Post.author.isInstaUser
    const dotStatus = isTagShop ? !(!CardStyle.timeStatus || authorStatus == 0 || isDetail) ? true : false : !(authorStatus == 0 || isDetail) ? true : false
    const timeStatus = isTagShop ? !(!CardStyle.timeStatus || isDetail) ? true : false : !(isDetail) ? true : (isSocialWall || isSocialEmbed) ? true : false
    
    return (
      <div className="tb_post_modal_post_author">
        <div className="tb_post_modal_author_details">
          {showAuthor ?
              <Author author={Post.author} networkId={Post.networkId} authorClass={`tb_post_modal_author_media tb-cTBfont-${CardStyle.author.variant}`} inView={true} />
            : null}

          <div className="tb_post_modal_author_deatils">
            {showAuthor ? <div className="tb_post_modal_author_wrap">
              <div className={`tb_post_modal_author_usrname tb-cTBfont-${CardStyle.author.variant}`} onClick={event => (Post.link && isEmbed) ? window.open(Post.link) : null}>{name} </div>
            </div> : null}
            <div className="tb_post_modal_author_info">
              {showAuthor ? <div className={`tb_post_modal_author_handlename tb-cTBfont-${CardStyle.author.variant}`}>{authorUserName}</div> : null}
              {dotStatus ? <div className="tb_post_modal_seprator_dot"><div></div></div> : null}
              {timeStatus ? <Time createdAt={Post.createdAt}  timeClass={`tb_post_modal_post_time tb-cTBfont-${CardStyle.author.variant}`} /> : null}
            </div>
          </div>
        </div>
        <div className="tb_post_modal_social_wrapper" onClick={event => (Post.link && isEmbed) ? window.open(ensureFullyQualifiedUrl(Post.link), "_blank") : null}>
          <Network networkClass={`tb_post_modal_social_ico tb-cTBfont-${CardStyle.author.variant}`} networkId={Post.networkId} iconStyle={CardStyle.icon} isPopUp={true} />
        </div>
      </div >
    );
  }
}
